* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Oswald', sans-serif;
  scroll-behavior: smooth;
}
.flex-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
section {
  padding: 50px;
  background-color: #f4f4f4;
}
button {
  padding: 7px 30px;
  background-color: fuchsia;
  cursor: pointer;
  border: none;
  box-shadow: 0 8px 15px 1px rgba(179, 179, 179, 0.5);
  border-radius: 2px;
  color: #f4f4f4;
  display: inline-block;
  font-size: 18px;
}
button:focus {
  outline: none;
  border: none;
  box-shadow: none;
}
.row {
  margin: 0;
  padding: 0;
}
.todo-section {
  background-color: #84a9ac;
}
.center-div {
  min-height: 80vh;
  width: 40%;
  background-color: #fff;
  border-radius: 12px;
  overflow: hidden;
  padding-bottom: 15px;
}
.center-div h1 {
  background-color: #3b6978;
  color: #fff;
  text-align: center;
  padding: 8px;
  margin: 0 0 5px 0;
  font-size: 1.7em;
  letter-spacing: 2px;
  border-radius: 12px 12px 0 0;
}
.dateTime-container {
  background-color: #3b6978;
  color: #fff;
  display: flex;
  justify-content: space-between;
  padding: 8px;
  margin: 5px 0;
}
.dateTime-container p {
  margin: 0;
}
.center-div .top-div {
  padding: 0 20px;
  margin: 15px 0;
}
.center-div .top-div input {
  width: 100%;
  height: 50px;
  font-size: 1.2em;
  border: none;
  text-align: center;
  border-bottom: 1px solid #b1b1b19c;
}
.center-div .top-div input:focus {
  outline: none;
}
.center-div .top-div button {
  padding: 0.8em 1.3em;
  border-radius: 45px;
  background-color: #3b6978;
  color: #fff;
  transition: all 1s ease;
}
.center-div .top-div button:hover {
  background-color: rgb(48, 194, 145);
}
.list-div {
  width: 100%;
  margin: 25px 0;
  padding: 0 20px;
}
.list-div li {
  list-style: none;
  font-size: 1.3em;
  color: #7e7e7e;
}
.item-row li {
  display: inline-block;
  margin: 0 8px;
}
.item-row i {
  color: #84a9ac;
  font-size: 18px;
}
.item-row i:first-of-type:hover {
  color: darkorange;
}
.fa-trash:hover {
  color: crimson;
}
.col-1,
.col-2,
.col-3 {
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 800px) {
  section {
    padding: 0;
  }
  .center-div {
    min-height: 100vh;
    width: 100%;
    border-radius: 0px;
  }
  .center-div .top-div {
    padding: 0 5px;
    margin: 15px 0;
  }
  .center-div h1 {
    border-radius: 0px;
  }
  .center-div .top-div button {
    padding: 0.9em 1.2em;
    border-radius: 45px;
  }
  .list-div {
    margin: 15px 0;
    padding: 0 5px;
  }
}
